<template>
  <div class="resume">
    <div id="name-title">
     <h1 id="name">Qiming Chen</h1>
     <h3 id="title">Software Developer|Data Scientist</h3>
    </div>
    <div id="skills">
     <h5 id="programming">Programming</h5>

    </div>
    <div id="statement">
     <h5 id="programming">Brand Statement</h5>
    </div>
    <div id="work">
     <h5 id="work-experience">work experience</h5>
    </div>
    <div id="education">
     <h5 id="programming">Education</h5>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Resume',
  mounted(){
    document.getElementById("skills").style.borderStyle = 'none';
  }
}
</script>

<style scoped>

div#name-title, div#skills, div#statement, div#work, div#education{
border-style: solid;
border-width: 5px;
border-color:#333;
border-radius: 10px;
margin-top: 20px;
}
div#skills:hover, div#statement:hover, div#work:hover, div#education:hover{
border-color:grey;
}

div.resume{
padding-left: 10%;
padding-right: 10%;
}

div#name-title{
-webkit-animation: slide-down .5s ease-out;
-moz-animation: slide-down .5s ease-out;
}

div#skills{
-webkit-animation: slide-left .9s ease-out;
-moz-animation: slide-left .9s ease-out;
}

div#statement{
-webkit-animation: slide-right 1.3s ease-out;
-moz-animation: slide-right 1.3s ease-out;

}

div#work{
-webkit-animation: slide-left 1.7s ease-out;
-moz-animation: slide-left 1.7s ease-out;

}

div#education{
-webkit-animation: slide-right 2.1s ease-out;
-moz-animation: slide-right 2.1s ease-out;

transition: opacity 3s ease;
transition-delay: 1.25s;

}

@-webkit-keyframes slide-down {
0% { opacity: 0; -webkit-transform: translateY(-100%); }
100% { opacity: 1; -webkit-transform: translateY(0); }
}
@-moz-keyframes slide-down {
0% { opacity: 0; -moz-transform: translateY(-100%); }
100% { opacity: 1; -moz-transform: translateY(0); }
}

@-webkit-keyframes slide-right {
0% { opacity: 0; -webkit-transform: translateX(-100%); }
100% { opacity: 1; -webkit-transform: translateX(0); }
}

@-moz-keyframes slide-right {
0% { opacity: 0; -webkit-transform: translateX(-100%); }
100% { opacity: 1; -webkit-transform: translateX(0); }
}

@-webkit-keyframes slide-left {
0% { opacity: 0; -webkit-transform: translateX(100%); }
100% { opacity: 1; -webkit-transform: translateX(0); }
}

@-moz-keyframes slide-left {
0% { opacity: 0; -webkit-transform: translateX(100%); }
100% { opacity: 1; -webkit-transform: translateX(0); }
}

</style>



