<template>
  <div class="nav-bar">
  <ul>
    <li>
      <router-link to="/">Home</router-link>
    </li>
    <li id="contact">
      <router-link to="/contact">Contact</router-link>
    </li>
    <li id="resume">
      <router-link to="/resume">Resume</router-link>
    </li>
  </ul>

  </div>
</template>

<script>
export default {
  name: 'Navbar'
}
</script>

<style scoped>
div.nav-bar{
  position: absolute;
  width:100%;
  top:0px;
  left:0px;
  background-color: #333;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #333;
}

li{
  float: left;
}

li a{
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

/* Change the link color to #111 (black) on hover */
li a:hover {
  background-color: #111;
}

li#contact{
  float:right;
}

li#resume{
  float:right;
}
</style>



