<template>
<section id="banner">
<div class="inner">
<header>
<h1 class="typewriter" id="name">This is Kyle.</h1>
<p class="typewriter" id="title">Software Engineer / Data Scientist</p>
</header>
</div>
</section>

</template>

<script>

export default {
  name: 'Banner'
}
</script>

<style scoped>
#banner {
padding: 8em 0 6em 0;
align-items: center;
justify-content: center;
background-image: url("../assets/banner1.jpg");
background-position: center;
background-size: cover;
background-repeat: no-repeat;
border-top: 0;
height: 30px;
position: relative;
text-align: center;
overflow: hidden;
}

#banner:before {
position: absolute;
content: '';
background-color: black;
opacity: 0.5;
width: 100%;
height: 100%;
top: 0;
left: 0;

-webkit-animation: wait-fade-out 10s;
-moz-animation: wait-fade-out 10s;
}

@-webkit-keyframes fade-in {
0% {
opacity: 0;
}
100% { opacity: 1;
}
}
@-moz-keyframes fade-in {
0% {
opacity: 0;
}
100% { opacity: 1;
}
}

@-webkit-keyframes fade-out {
0% {
opacity: 1;
}
100% { opacity: 0;
}
}

@-moz-keyframes fade-out {
0% {
opacity: 1;
}
100% { opacity: 0;
}
}

@-webkit-keyframes wait-fade-out {
0% {
opacity: 1;
}
50% {
opacity: 0.9;
}
100% { opacity: 0.5;
}
}

@-moz-keyframes wait-fade-out {
0% {
opacity: 1;
}
50% {
opacity: 0.9;
}
100% { opacity: 0.5;
}
}

.typewriter {
overflow: hidden; /* Ensures the content is not revealed until the animation */
//border-right: .15em solid orange; /* The typwriter cursor */
white-space: nowrap; /* Keeps the content on a single line */
margin: 0 auto; /* Gives that scrolling effect as the typing happens */
letter-spacing: .15em; /* Adjust as needed */
//animation: 
// typing 3.5s steps(40, end),
//blink-caret .75s step-end infinite;
animation: 
typing 5s steps(40, end);
}

/* The typing effect */
@keyframes typing {
from { width: 0 }
to { width: 100% }
}

/* The typewriter cursor effect */
/*@keyframes blink-caret {
from, to { border-color: transparent }
50% { border-color: orange; }
}*/

#name{
position: relative;
z-index:1;
color:white;
text-weight:900;

}

#title{
position: relative;
z-index:1;
color:white;
animation-delay:3s;
}

</style>



