<template>
  <div id= "intro">
    <ul>
      <li>
        <div id="intro-left-column">
          <img id="round-image" src="../assets/photo.jpg" alt="" />
        </div>
      </li>
      <li>
        <div id="intro-right-column">
          <p> content </p>
          <button type="button"> Learn More </button>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'HomeIntro'
}
</script>

<style scoped>

li{
  display: inline;
}



div#intro{
position:relative;
top:10px;
}

div#intro-left-column{
  width:25%;
  float:left;
}

div#intro-right-column{
  border-style: solid;
  border-color:black;
  width:70%;
  float:right;
}

@media screen and (max-width: 600px) {
div#intro-left-column{
  width:100%;
  float:center;
}

div#intro-right-column{
  width:100%;
  float:center;
}
}

img#round-image{
    background: white;
    background-repeat: no-repeat;
    background-size: cover;
    overflow:hidden;
    -webkit-border-radius:50%;
    -moz-border-radius:50%;
    border-radius:50%;
    width:150px;
    height:150px;
}

</style>



